.order-container {
    font-family: Arial, sans-serif;
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 5px;
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);
}

.title {
    text-align: center;
    color: #333;
    margin-bottom: 20px;
}

.order-wrapper {
    display: flex;
    flex-direction: column;
}

.table {
    font-size: 1.5em;
    color: #333;
    margin-bottom: 20px;
}

.order-items {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 20px;
    margin-bottom: 20px;
    background-color: #fff;
}

.order-item {
    border-bottom: 1px solid #ddd;
    padding: 10px 0;
    margin-bottom: 10px;
}

.order-item:last-child {
    border-bottom: none;
}

.order-id, .order-status {
    width: 200px; /* adjust this value as needed */
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    text-align: left;
}

.order-item ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.order-item ul li {
    margin-bottom: 5px;
    font-size: 1em;
    width: 200px; /* adjust this value as needed */
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    text-align: left;
}

.order-actions {
    display: flex;
    justify-content: space-between;
}

.confirm-button {
    padding: 10px 20px;
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 5px;
}